import styled from "styled-components";
import { Link } from "gatsby";
import React from "react";

const Feed = styled.section`
  margin-bottom: 32px;
`;

const FeedMeta = styled.div``;

const FeedDescription = styled.p`
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 19.5px;
`;

const FeedTitle = styled(Link)`
  font-size: 25px;
  color: black;
  text-decoration: none;
  &:hover,
  &:focus {
    color: gray;
  }
`;

const FeedTime = styled.time`
  font-size: 14px;
  color: #222;
  font-weight: 600;
  text-transform: uppercase;
`;

const FeedCategoryLink = styled(Link)`
  font-size: 14px;
  color: #5d93ff;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    color: gray;
  }
`;

const FeedTags = ({ tags }) => (
  <>
    {tags.map((tag, index) => {
      const comma = index === 0 ? "" : ",";

      return (
        <span key={index}>
          {comma}
          <FeedCategoryLink to={`/tags/${tag}`}> {tag}</FeedCategoryLink>
        </span>
      );
    })}
  </>
);
const BlogFeedCard: React.FC<{ frontmatter: any; fields: any }> = ({
  frontmatter,
  fields,
}) => {
  const { title, description, date, tags } = frontmatter;
  return (
    <Feed>
      <FeedTitle to={fields.slug}>{title}</FeedTitle>
      <FeedMeta>
        <FeedTime>{date}</FeedTime>
        {tags && <FeedTags tags={tags} />}
      </FeedMeta>
      <FeedDescription>{description}</FeedDescription>
    </Feed>
  );
};

export default BlogFeedCard;
